:root {
  --color_white: #fff;
  --color_dark: #1f1e1d;
  --color_light_grey: #4d5051;
  --color_light_blue: #f5f7fb;
  --color_yellow: #ffcb09;
  --font_main: "Montserrat", sans-serif;
  --transition: all 0.2s ease-out;
}

/*===== global styles =====*/
html {
  box-sizing: border-box;
}

*,
::after,
::before {
  box-sizing: inherit;
}

body {
  font-family: var(--font_main);
  color: var(--color_dark);
}

body.lock {
  overflow: hidden;
}

.container {
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 30px;
}

a {
  color: inherit;
  text-decoration: none;
  outline: none;
}

html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul,
li {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}

ol,
ul {
  list-style: none;
}

button,
input {
  font-family: var(--font_main);
  border: none;
  appearance: none;
  outline: none;
  background-color: transparent;
}

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

.block__title {
  font-weight: 600;
  font-size: 36px;
  line-height: 120%;
  color: #1f1e1d;
}

.btn__link {
  width: 200px;
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  font-weight: 500;
  font-size: 16px;
  line-height: 120%;
  color: #1f1e1d;
  transition: background-color 0.2s ease-in-out;
  cursor: pointer;
}

.btn__yellow {
  background: #ffcb09;
  cursor: pointer;
}
.btn__yellow:hover {
  background: #ffd741;
}

.btn__grey {
  cursor: pointer;
  background: #efefef;
}
.btn__grey:hover {
  background: #ffcb09;
}

/*===== max-width 1279px =====*/
@media (max-width: 1279px) {
  .container {
    max-width: 100%;
    padding: 0 20px;
  }

  .block__title {
    font-size: 30px;
  }
}
/*===== max-width 767px =====*/
@media (max-width: 767px) {
  .container {
    padding: 0 10px;
  }

  .block__title {
    font-size: 24px;
  }
}
* {
  box-sizing: border-box;
}

*,
p,
h1,
h2,
h3 {
  padding: 0;
  margin: 0;
}

body {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 320px;
  height: 100%;
  min-height: 100vh;
  background-color: #fff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/*===== header styles =====*/
.header {
  box-shadow: 0px 4px 15px rgba(22, 37, 55, 0.1);
}
.header .container {
  max-width: 1718px;
  display: flex;
  justify-content: space-between;
}

.header__top {
  padding: 9px 0 8px;
  border-bottom: 1px solid #e3e3e3;
}

.header__top .container,
.header__bottom .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
}

.header__menu ul {
  display: flex;
  align-items: center;
}

.header__elem {
  position: relative;
  margin-right: 70px;
  cursor: pointer;
}

.header__link {
  display: block;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #1f1e1d;
  transition: color 0.2s ease-in-out;
}
.header__link:hover {
  color: #ffcb09;
}
.header__link:last-child {
  margin-right: 0;
}
.header__link svg {
  margin-left: 8px;
}
.header__link path {
  transition: stroke 0.2s ease-in-out;
}
.header__link:hover path {
  stroke: #ffcb09;
}

.dropdown_menu {
  position: absolute;
  top: 32px;
  left: 0;
  z-index: 5;
  width: 274px;
  padding: 30px 18px 35px 18px;
  background: #fff;
  box-shadow: 0px 4px 15px rgba(22, 37, 55, 0.1);
  border-radius: 0px 0px 10px 10px;
  cursor: default;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
}
.dropdown_menu.dropdown__list {
  display: block;
}
.dropdown_menu li {
  display: block;
  margin-bottom: 20px;
}
.dropdown_menu li:last-child {
  margin-bottom: 0;
}

.dropdown_show::after {
  display: none;
  content: "";
  position: absolute;
  bottom: -16px;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 20px;
  cursor: default;
}
.dropdown_show:hover .header__list {
  color: #ffcb09;
}
.dropdown_show:hover .header__list path {
  stroke: #ffcb09;
}
.dropdown_show.active::after {
  display: block;
}
.dropdown_show.active .dropdown_menu {
  visibility: visible;
  opacity: 1;
}
.dropdown_show.active .header__list svg {
  transform: scaleY(-1);
}

.header__list svg {
  transition: all 0.2s linear;
}

.header__search {
  display: flex;
  align-items: center;
  width: 33%;
  height: 36px;
  background: #f5f7fb;
  border-radius: 100px;
  cursor: pointer;
}
.header__search img {
  margin: 0 14px 0 30px;
}
.header__search p {
  flex: 1;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #999;
}

.header__logo {
  position: relative;
  width: 203px;
  height: 39px;
}
.header__logo a {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
}

.header__logo--big {
  display: block;
  width: 100%;
  height: auto;
}

.header__logo--small {
  display: none;
  width: 100%;
  height: auto;
}

.header__contacts {
  display: flex;
  align-items: center;
}

.header__item {
  position: relative;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #1f1e1d;
  min-width: max-content;
}
.header__item:nth-child(1) {
  margin-right: 49px;
}
.header__item:nth-child(2) {
  margin-right: 63px;
}
.header__item:nth-child(3) {
  margin-right: 59px;
}

.header__contact {
  transition: all 0.2s ease-in-out;
}

.header__messengers {
  position: absolute;
  top: -20px;
  right: -52px;
  display: flex;
  align-items: flex-end;
}
.header__messengers svg {
  cursor: pointer;
}

.header__whatsapp,
.header__viber {
  position: relative;
}
.header__whatsapp a,
.header__viber a {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
}
.header__whatsapp path,
.header__viber path {
  transition: fill 0.2s ease-in-out;
}

.header__viber {
  position: relative;
  top: 2px;
}

.header__item:hover .header__contact {
  color: #ffcb09;
}

.header__item:hover .header__whatsapp path:first-child {
  fill: #00e676;
}

.header__item:hover .header__viber path:first-child {
  fill: #7b519c;
}

/*===== header small menu styles =====*/
.header__menu-small {
  display: none;
}

.header__search-small {
  cursor: pointer;
  margin-right: 30px;
}

.header__burger {
  position: relative;
  width: 21px;
  height: 16px;
  cursor: pointer;
}

.header__burger span {
  position: absolute;
  background-color: #1f1e1d;
  left: 0;
  width: 100%;
  height: 2px;
  top: 7px;
  border-radius: 100px;
  transition: all 0.3s ease 0s;
}

.header__burger:before,
.header__burger:after {
  content: "";
  background-color: #1f1e1d;
  position: absolute;
  width: 100%;
  height: 2px;
  left: 0;
  border-radius: 100px;
  transition: transform 0.3s ease 0s;
}

.header__burger:before {
  top: 0;
}

.header__burger:after {
  bottom: 0;
}

.header__burger.active:before {
  transform: rotate(45deg);
  top: 7px;
}

.header__burger.active:after {
  transform: rotate(-45deg);
  bottom: 7px;
}

.header__burger.active span {
  transform: scale(0);
}

/*===== header search styles =====*/
.header__search-frame {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1100;
  background-color: rgba(0, 0, 0, 0.12);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
}
.header__search-frame.active {
  visibility: visible;
  opacity: 1;
}

.header__search-field {
  position: relative;
  width: 664px;
  max-height: 475px;
  max-height: 80vh;
  margin: 52px auto 0;
  background: #fff;
  border-radius: 10px;
}

.header__search-closer {
  position: absolute;
  right: -25px;
  top: -25px;
  z-index: 10;
  cursor: pointer;
}

.header__search-top {
  width: 100%;
  height: 94px;
  padding: 21px 16px 0 16px;
  box-shadow: 0px 4px 15px rgba(22, 37, 55, 0.1);
}

.header__search-request input {
  width: 100%;
  margin-bottom: 30px;
  font-size: 14px;
  line-height: 17px;
  color: #1f1e1d;
}
.header__search-request input::placeholder {
  font-size: 14px;
  line-height: 17px;
  color: #999;
}

.header__search-categories {
  display: flex;
}

.header__search-category {
  position: relative;
  margin-right: 20px;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #999;
  cursor: pointer;
  transition: color 0.2s ease-in-out;
}
.header__search-category:last-child {
  margin-right: 0;
}
.header__search-category.active {
  color: #1f1e1d;
}
.header__search-category.active::after {
  content: "";
  position: absolute;
  bottom: -7px;
  left: 0;
  width: 100%;
  height: 2px;
  border-radius: 1000px;
  background-color: #1f1e1d;
}

.header__search-output {
  height: calc(80vh - 94px);
  max-height: 381px;
  overflow: auto;
  padding: 4px 16px;
}

.header__search-init {
  display: none;
  padding-top: 16px;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: #1f1e1d;
}
.header__search-init.active {
  display: block;
}

.header__search-item {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 19px 0 16px;
  border-bottom: 1px solid rgba(227, 227, 227, 0.5);
  color: #1f1e1d;
  transition: color 0.2s ease-in-out;
}
.header__search-item:last-child {
  border: none;
}
.header__search-item:hover {
  color: #ffcb09;
}
.header__search-item a {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 10;
  outline: none;
}

.header__search-name {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
}

.header__search-price {
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  white-space: nowrap;
}

/*===== max-width 1600px =====*/
@media (max-width: 1600px) {
  .header .container {
    max-width: 1230px;
  }

  .header__elem {
    margin-right: 40px;
  }

  .header__search {
    width: 360px;
  }
}
/*===== max-width 1279px =====*/
@media (max-width: 1279px) {
  .header {
    position: relative;
    z-index: 10;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 13px 20px;
  }
  .header.header__fixed::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    background-color: #fff;
  }

  .header .container {
    max-width: 100%;
    padding: 0;
  }

  .header__wrapper {
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    z-index: -1;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    padding: 50px 20px 29px;
    background-color: #fff;
    transition: all 0.2s ease-out;
  }
  .header__fixed .header__wrapper {
    transform: translateX(100%);
  }

  .header__top {
    flex-grow: 1;
    border: none;
    padding-bottom: 0;
    margin-bottom: 43px;
  }

  .header__search {
    display: none;
  }

  .header__logo {
    width: 115px;
    height: 24px;
  }

  .header__logo--big {
    display: none;
  }

  .header__logo--small {
    display: block;
  }

  .header__menu-small {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .header__menu ul {
    display: block;
  }

  .header__elem:not(:last-child) {
    margin-bottom: 20px;
  }

  .header__contacts {
    gap: 0 50px;
  }

  .header__item:nth-child(n) {
    margin-right: 0;
  }
  .header__item:nth-child(1) {
    order: 1;
  }
  .header__item:nth-child(2) {
    order: 2;
  }
  .header__item:nth-child(3) {
    order: 4;
  }
  .header__item:nth-child(4) {
    order: 3;
  }

  .header__link {
    font-size: 18px;
    line-height: 100%;
    font-weight: 500;
  }

  .dropdown_menu {
    max-height: 0;
    overflow: hidden;
    position: static;
    visibility: visible;
    opacity: 1;
    padding: 0;
    box-shadow: none;
    border-radius: 0;
    transition: all 0.2s ease-out;
  }
  .dropdown_menu .header__link {
    font-size: 14px;
    font-weight: 400;
  }
  .dropdown_menu .header__link:first-child {
    margin-top: 16px;
  }

  .dropdown_show.active::after {
    display: none;
  }
}
/*===== max-width 767px =====*/
@media (max-width: 767px) {
  .header__search-field {
    width: 80vw;
  }

  .header__wrapper {
    padding: 50px 10px 20px;
  }

  .header__contacts {
    display: grid;
    grid-template-areas: "header__item__1 header__item__3" "header__item__2 header__item__4";
    gap: 16px 27px;
  }

  .header__item:nth-child(1) {
    grid-area: header__item__1;
  }
  .header__item:nth-child(2) {
    grid-area: header__item__2;
  }
  .header__item:nth-child(3) {
    grid-area: header__item__3;
  }
  .header__item:nth-child(4) {
    grid-area: header__item__4;
  }
}
/*===== max-width 479px =====*/
@media (max-width: 479px) {
  .header__search-field {
    width: 98vw;
  }

  .header__search-closer {
    top: -30px;
    right: 50%;
    transform: translateX(50%);
  }

  .header__contact {
    font-size: 12px;
    line-height: 100%;
  }

  .header__link {
    font-size: 14px;
  }

  .dropdown_menu .header__link {
    font-size: 12px;
  }
}
.main {
  flex-grow: 1;
}

/*===== intro styles =====*/
.intro {
  padding-top: 122px;
  background: url("./../images/intro/intro-bg.jpg") center/cover no-repeat;
}

.intro__wrapper {
  display: flex;
  justify-content: space-between;
}

