  transition: box-shadow 0.3s ease-in-out;
  overflow: hidden;
}
.machines__intro:hover {
  box-shadow: 0px 4px 15px rgba(22, 37, 55, 0.1);
}

.machines__intro-bg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  z-index: -1;
  background: url("./../images/machines/machines__bg.jpg") center/cover no-repeat;
  transition: all 0.4s ease-in-out;
}

.machines__intro:hover .machines__intro-bg {
  width: 103%;
  height: 103%;
}

.machines__header,
.machines__subheader {
  margin: 0 auto;
  text-align: center;
}

.machines__header {
  margin-bottom: 20px;
  font-weight: 600;
  font-size: 20px;
  line-height: 120%;
  color: #fff;
}

.machines__subheader {
  width: 52%;
  font-size: 14px;
  line-height: 170%;
  color: #fff;
}

.machines__wrapper {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(3, 70px);
  gap: 30px;
  margin-bottom: 30px;
}

.machines__item {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  border: 1px solid rgba(227, 227, 227, 0.5);
  border-radius: 6px;
  transition: box-shadow 0.2s ease-in-out;
}
.machines__item:hover {
  box-shadow: 0px 4px 15px rgba(22, 37, 55, 0.1);
}

.machines__link {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
}

.machines__name {
  text-align: center;
  padding: 13px 24px;
  font-weight: 600;
  font-size: 16px;
  line-height: 170%;
  color: #4d5051;
}

.machines__text {
  width: 86%;
  margin-bottom: 10px;
  font-size: 14px;
  line-height: 170%;
  color: #4d5051;
}
.machines__text:last-child {
  margin-bottom: 0;
}

/*===== reviews styles =====*/
.reviews {
  padding-bottom: 150px;
}

.reviews__title {
  margin-bottom: 30px;
}

.reviews__slider-container,
.reviews__slider {
  position: relative;
  width: 100%;
  min-height: 300px;
}

.reviews__slider {
  z-index: 3;
  margin-bottom: 30px;
}

.reviews__preview {
  position: relative;
  z-index: 3;
  width: 31.62%;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  background-color: #999;
  border-radius: 10px;
  cursor: pointer;
}
.reviews__preview:hover img {
  transform: scale(1.2);
}

.reviews__img {
  height: 80%;
}
.reviews__img img {
  display: block;
  width: auto;
  height: 100%;
  transition: all 0.2s ease-in-out;
}

.reviews__item {
  width: 100%;
  display: flex;
}

.reviews__info {
  position: relative;
  width: 68.38%;
  min-height: 300px;
  box-sizing: border-box;
  padding: 63px 65px 70px 36px;
  border: 1px solid rgba(227, 227, 227, 0.5);
  border-radius: 10px;
  transition: all 0.2s ease-in-out;
}
.reviews__info:hover {
  box-shadow: 0px 4px 15px rgba(22, 37, 55, 0.1);
}

.reviews__header {
  margin-bottom: 20px;
  font-weight: 600;
  font-size: 20px;
  line-height: 120%;
  color: #1f1e1d;
}

.reviews__text {
  font-size: 14px;
  line-height: 170%;
  color: #4d5051;
}

.reviews__link {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 3;
}

.reviews__nav {
  position: absolute;
  top: 50%;
  left: 0;
  z-index: 2;
  width: 100%;
  display: flex;
  justify-content: space-between;
  transform: translateY(-50%);
}

.reviews__prev,
.reviews__next {
  position: relative;
  min-width: 40px;
  min-height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  border: none;
  appearance: none;
  background-color: transparent;
  cursor: pointer;
}
.reviews__prev img,
.reviews__next img {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 2;
  transform: translate(-50%, -50%);
}

.reviews__prev {
  left: -70px;
}

.reviews__next {
  right: -70px;
}

.reviews__navButton {
  display: inline-block;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  box-shadow: 0px 4px 15px rgba(22, 37, 55, 0.1);
  transition: all 0.2s ease-in-out;
}

.reviews__prev:hover .reviews__navButton,
.reviews__next:hover .reviews__navButton {
  transform: scale(1.1);
}

.reviews__pagination {
  display: none;
  justify-content: center;
}

.reviews__dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  margin-right: 5px;
  background-color: #c4c4c4;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
}
.reviews__dot:hover {
  background-color: #666;
}
.reviews__dot:last-child {
  margin-right: 0;
}

.reviews__dot-active {
  background-color: #ffcb09;
}
.reviews__dot-active:hover {
  background-color: #ffcb09;
}

.reviews__fullscreen {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1500;
  background-color: rgba(153, 153, 153, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  transition: opacity 0.2s ease-in-out;
  visibility: hidden;
  opacity: 0;
}
.reviews__fullscreen.active {
  visibility: visible;
  opacity: 1;
}

.reviews__fullscreen-wrapper {
  position: relative;
  height: 90vh;
}

.reviews__fullscreen-img {
  display: block;
  height: 100%;
  width: auto;
}

.reviews__fullscreen-closer {
  position: absolute;
  top: -25px;
  right: -25px;
  cursor: pointer;
}

/*===== feedback styles =====*/
.feedback.second__feedback {
  padding: 80px 0 100px;
  background-color: #f5f7fb;
}

/* появление блоков при скролле */
.element-animation {
  opacity: 0;
  transform: translateY(20px);
}

.element-animation.element-show {
  opacity: 1;
  transition: all 1s;
  transform: translateY(0%);
}

/* Стили при работе с формами */
input::placeholder {
  transition: color 0.2s ease-in-out;
}

input:focus::placeholder {
  color: transparent;
}

form {
  position: relative;
}

.error__message {
  position: absolute;
  top: -23px;
  left: 0;
  font-size: 12px;
  line-height: 170%;
  color: #fc3a3a;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
}
.error__message.show {
  visibility: visible;
  opacity: 1;
}

/*==== animations ====*/
@keyframes addItems {
  from {
    opacity: 0;
    transform: translateY(5px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
