    margin-bottom: 24px;
  }
}

.filters__current {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
}

.filters__elem,
.filters__clear {
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  border: 1px solid #e3e3e3;
  border-radius: 17px;
  color: var(--color_dark);
}

.filters__elem {
  position: relative;
  padding: 7px 34px 9px;
}

.filters__elemText.animated {
  animation: changeColor 0.8s linear;
}

@keyframes changeColor {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.4;
  }
  100% {
    opacity: 1;
  }
}
.filters__elemRemove {
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
}
.filters__elemRemove:path {
  transition: var(--transition);
}
.filters__elemRemove:hover path {
  stroke: var(--color_dark);
}

.filters__clear {
  display: none;
  padding: 7px 12px 9px;
  background-color: transparent;
  border: 1px solid var(--color_yellow);
  cursor: pointer;
}
.filters__current.active .filters__clear {
  display: inline-block;
}

.filters__show {
  position: relative;
  z-index: 4;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  transition: var(--transition);
}
.filters__show path, .filters__show circle {
  transition: var(--transition);
}
.filters__show:hover, .filters__show.active {
  color: var(--color_yellow);
}
.filters__show:hover path, .filters__show:hover circle, .filters__show.active path, .filters__show.active circle {
  stroke: var(--color_yellow);
}

.filters__counter {
  position: absolute;
  top: -11px;
  right: -9px;
  width: 14px;
  height: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  font-weight: 500;
  font-size: 11px;
  line-height: 13px;
  color: var(--color_dark);
  background-color: var(--color_yellow);
  cursor: default;
  pointer-events: none;
}

