@keyframes logoHover {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.03);
  }
}
@keyframes textFadeIn {
  from {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
  }
  to {
    opacity: 1;
    visibility: visible;
    pointer-events: all;
  }
}
.partners__item {
  position: relative;
  overflow: hidden;
  border: 1px solid rgba(227, 227, 227, 0.5);
  border-radius: 10px;
}

.partners__logo {
  width: 100%;
  height: auto;
  transition: all 0.2s ease-out;
}
.partners__item:hover .partners__logo {
  animation: logoHover 0.4s ease-out forwards;
}

.partners__info {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  padding: 14px 4px;
  background-color: #fff;
  border: 1px solid rgba(227, 227, 227, 0.5);
  border-radius: 10px;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: all 0.4s ease-out;
}
.partners__item:hover .partners__info {
  animation: textFadeIn 0.3s ease-out 0.3s forwards;
}

.partners__about {
