/*===== footer styles =====*/
.footer {
  padding: 43px 0 0;
}

.footer__logo {
  max-width: 252px;
  position: relative;
  margin-bottom: 30px;
}
.footer__logo a {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
}

.footer__wrapper {
  display: grid;
  grid-template-areas: "info nav" "other nav" "social nav";
  grid-template-columns: 330px 165px;
  grid-template-rows: 180px 67px 30px;
  gap: 43px 370px;
  margin-bottom: 33px;
}

.footer__info {
  grid-area: info;
}

.footer__nav {
  grid-area: nav;
}

.footer__other {
  grid-area: other;
}

.footer__social {
  grid-area: social;
  display: flex;
  justify-content: space-between;
}

.ft_link {
  transition: color 0.2s ease-in-out;
}
.ft_link:hover {
  color: #ffcb09;
}

.footer__text {
  position: relative;
  margin-bottom: 15px;
  font-size: 14px;
  line-height: 170%;
  color: #4d5051;
}
.footer__text:last-child {
  margin-bottom: 0;
}
.footer__text path {
  transition: fill 0.2s ease-in-out;
}
.footer__text:hover path {
  fill: #ffcb09;
}

.footer__icon {
  margin-right: 8px;
}

.footer__cLink {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
}

.footer__link {
  display: block;
  width: 100%;
  margin-bottom: 33px;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #1f1e1d;
}

.footer__el {
  display: block;
  width: 100%;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #1f1e1d;
}
.footer__el:first-child {
  margin-bottom: 23px;
}

.footer__item {
  position: relative;
}
.footer__item a {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
}
.footer__item path {
  transition: color 0.2s ease-in-out;
}
.footer__item:hover path {
  fill: #ffd741;
}

.footer__bottom {
  padding: 20px 0 21px;
  background-color: #f5f7fb;
}

.footer__bottom .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__company,
.footer__dev {
  font-size: 14px;
  line-height: 170%;
  color: #4d5051;
}
.footer__company a,
.footer__dev a {
  transition: color 0.2s ease-in-out;
}
.footer__company a:hover,
.footer__dev a:hover {
  color: #1f1e1d;
}

